<template>
	<view>
		<!-- 自定义导航栏 -->
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }" title="TimeLaw" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
			<u-top-tips ref="uTips" :navbar-height="statusBarHeight + navbarHeight"></u-top-tips>
		</view>
		<view class="login">
			
			<!--------------------------------测试入口  -->
			<view class="title" @click="loginTest()">
				登<span>录</span>
			</view>
			
			<view class="input">
				<view id="username">用户名</view>
				<view class="inputUsername">
					<u-input v-model="loginForm.username" type="text" border="true" placeholder="请输入用户名" />
				</view>
			</view>
			
			<view class="input">
				<view id="password">密码</view>
				<view class="inputPassword">
					<u-input v-model="loginForm.password" type="password" border="true" password-icon="true" placeholder="请输入密码" /> 
				</view>
			</view>
			
			<view class="submitButton" >
				<u-button type="primary" @click="login()">登录</u-button> 
			</view>
			
			<view class="or">
				<u-divider color="#707070" border-color="#BA55D3" half-width="70" bg-color="#f1f1f1">或者</u-divider>
			</view>
			
			<view class="submitButton" >
				<u-button type="success" @click="wechatLogin()">微信用户一键登录</u-button> 
			</view>
			
			
			<view class="gotoRegist" :style="{marginTop: floatUp + 'rpx'}" @click="goToRegistPage()">
				没有账号，<span>立即注册</span>
			</view>
			
		</view>
		
		
		
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 状态栏高度，H5中，此值为0，因为H5不可操作状态栏
				statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
				// 导航栏内容区域高度，不包括状态栏高度在内
				navbarHeight: 44,
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				},
				loginForm:{
					username:'sunuser',
					password:'123456'
				},
				floatUp: '550'
			}
		},
		onLoad() {
			var token = uni.getStorageSync('token');
			if(token!= null && token != ''){				 
				uni.request({
					url:'http://localhost/users/beforeLogin',
					method:"GET",
					header:{
						"token": token
					},
					success: (e) => {
						console.log(e);
						if(e.data.flag == true){
							uni.switchTab({
								url: '../index/index'
							});
						}
					}
				})
			}
		},
		onReady() {
			this.$refs.uTips.show({
				title: "为了丰富您的体验，请先登录",
				type: 'primary'
			});
			// 监听键盘高度变化，以便设置输入框的高度
			uni.onKeyboardHeightChange(res => {
				var height = 550 - res.height;
				this.floatUp = height;
			});
		},
		methods:{
			// 测试------------------------
			loginTest(){
				uni.switchTab({
					url: '../index/index'
				});
			},
			
			login(){
				uni.request({
					url:'http://localhost/users/login',
					method:"POST",
					data:{
						username: this.loginForm.username,
						password: this.loginForm.password
					},
					success: (e) => {
						console.log(e);
						if(e.data.flag){
							uni.setStorageSync('token',e.data.data.token);
							uni.navigateBack({
								
							});
							// uni.switchTab({
							// 	url: '../index/index'
							// });							
						}else{
							console.log(e.data.msg);
							this.$refs.uTips.show({
								title: e.data.msg,
								type: 'error'
							});
						}
						
					}
				})
			},
			wechatLogin(){ 
				
				//重新登录
				let code = ''
				uni.login({
					provider: 'weixin',
					success: function(res) {
						code = res.code;
						console.log('login重新登录',{
							code: res.code,
							codeinfo:res
						});
					},
					fail: function(res) {
						console.log(res)
					}
				});
					
				// 获取用户信息
				uni.getUserProfile({
					desc:'获取你的昵称、头像、地区及性别',
					success: (res) => {
						// console.log('测试：获取你的昵称、头像',res);
						uni.request({
							url:'http://localhost/users/wxLogin',
							method:"POST",
							data:{
								code: code,
								nickName: res.userInfo.nickName,
								avatar:res.userInfo.avatarUrl
							},
							success: (e) => {
								 console.log("测试：向后端请求成功",e);
								//缓存返回的token
								 uni.setStorageSync('token',e.data.data.token);				
								 // uni.switchTab({
								 // 	url:'../index/index'
								 // });
								 uni.navigateBack({
								 	
								 });
							},
							fail: () => {
								uni.showModal({
									title: '登录失败',
									content: '请刷新小程序后重新操作',
								});
							}
						})
					},
					fail: (res) => {
						console.log(res);
						//拒绝授权
						uni.showToast({
							title: '您拒绝了授权请求',
							icon: 'error',
							duration: 2000
						});
						return;
					}
				});
				
			},
			
			goToRegistPage(){
				uni.navigateTo({
					url:'../regist/regist'
				});
			}
		}
	}
</script>

<style scoped>
	.login{
		width: 700rpx;
		margin-left: 25rpx;
		/* background-color: white; */
		margin-top: 80rpx;
	}
	.title{
		font-size: 90rpx;
		color: #1E90FF;
		width: 500rpx;
		padding-left: 20rpx;
		/* margin-left: 50rpx; */
		border-bottom: 1px solid #1E90FF;
	}
	.title span{
		margin-left: 25rpx;
		font-size: 38rpx;
		color: #BA55D3;
	}
	.inputPassword{
		width: 550rpx;
		/* border: 1px solid #000; */
	}
	.inputUsername{
		width: 550rpx;
	}
	.input{
		display: flex;
		margin-top: 30rpx;
	}
	#username,#password{
		font-size: 30rpx;
		height: 70rpx;
		line-height: 70rpx;
		margin-right: 30rpx;
	}
	#password{
		margin-left: 30rpx;
	}
	.submitButton{
		margin-top: 50rpx;
		width: 500rpx;
		margin-left: 100rpx;
	}
	.gotoRegist{
		margin-left: 235rpx;
		/* margin-top: 700rpx; */
	}
	.gotoRegist span{
		color: #1E90FF;
		text-decoration: underline;
	}
	.or{
		margin-top: 25rpx;
	}
	
	
	
	.wechat{
		border: 1px solid #000;
	}
	.wechat img{
		width: 80rpx;
		height: 80rpx;
	}
</style>